import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { List } from 'antd';
import './App.css';
import Map from './Map';
import CardGame from './CardGame';

function Home() {
  return (
    <List>
      <List.Item><Link to='/game'>Game</Link></List.Item>
      <List.Item><Link to='/map'>Map</Link></List.Item>
    </List>
  )
}

function App() {
  return (
    <div className="App">
      <Router>
        <Routes>
          <Route path="/game/*" element={<CardGame />} />
          <Route path="*" element={<Map />} />
          <Route path="*" element={<Home />} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
